.password-btn{
  background: #00cabc;
  border: #00cabc;
  width: 90%;
  margin: 0 auto;
}

.password-input{
  text-align: left;
  border: 0.01rem solid #dcdcdc;
  width: 90%;
  margin:0.6rem auto;
  border-radius: 0.04rem;
  height: 0.8rem;
  line-height: 0.8rem;
}

.password-input input{
  width: 90%;
  padding-left: 0.2rem;
  border:none;
}

.am-steps-item.am-steps-status-process .am-steps-head-inner{
  background: #00cabc;
  border: #00cabc;
}
.password-step{
  width: 33.3%;
  float: left;
  color: #000;
  background: #e5e5e5;
}

.password-top{
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  line-height: 0.7rem;
  text-align: center;
  width: 90%;
  margin: 0 auto;
  margin-top: 2rem;
}

.password-step-active{
  background: #00cabc;
  border: #00cabc;
  color: #fff;
}
.password-title{
  text-align: center;
  margin-top: 0.2rem;
}

.shaft{
  margin-left: auto;
  margin-right: auto;
  width:0.3rem;
  height:0.02rem;
  background-color:black;
}

.arrowhead{
  margin-left: auto;
  margin-right: auto;
  width: 0;
  height: 0;
  border-left: 0.4rem solid transparent;
  border-right: 0.4rem solid transparent;
  border-top: 0.5rem solid black;
}

.password-verify{
  width: 55%;
  margin-left: 0.4rem;
}

.password-verify-right{
  text-align: left;
  width: 32%;
  margin:0.6rem auto;
  border-radius: 0.04rem;
  height: 0.8rem;
  line-height: 0.8rem;
  float: right;
  margin-right: 0.4rem;
  background: url("../image/verify.png") no-repeat;
  background-size:cover;
}


