html body {
  background-color: #fff;
}

.login-title {
  font-size: 0.4rem;
  width: 100%;
  text-align: center;
  height: 2rem;
  background-color: #00cabc;
  margin-bottom: 0.3rem;
  color: #fff;
  line-height: 2rem;
}

.login-input {
  width: 90%;
  margin: auto;
  border: 0.01rem solid #ccc;
  border-radius: 0.1rem;
  height: 0.8rem;
  line-height: 0.4rem;
  background-color: #fff;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}

.login-user i {
  color: #00cabc;
  font-size: 0.3rem;
  font-weight: bold;
}

.login-password {
  margin-top: 0.3rem;
}

.login-password i {
  color: #00cabc;
  font-size: 0.42rem;
}

.login-input input {
  height: 0.4rem;
  width: 3rem;
  border: none;
  font-size: 0.3rem;
  margin-top: 0.16rem;
  -webkit-box-flex: 1;
  -webkit-flex: 1;
      -ms-flex: 1;
          flex: 1;
}

.login-input > div {
  display: inline-block;
  width: 0.8rem;
  text-align: center;
  padding-left: 0.1rem;
  height: 0.8rem;
  line-height: 0.8rem;
}

.login-password > input {
  position: relative;
  bottom: 0.05rem;
}

.login-btn {
  background-color: #00cabc !important;
  color: #fff !important;
  width: 90%;
  margin: 0.9rem auto 0;
  border: none !important;
}

.login-btn-active {
  background-color: #2bc6dc;
}

.login-patriarch {
  text-align: right;
  width: 90%;
  margin: 0.3rem auto 0;
}

.login-patriarch a {
  color: #00cabc;
}

.login-password-block {
  width: 90%;
  margin: 0.3rem auto;
}

.login-forget {
  float: right;
  color: #999;
}

.login-checkbox {
  float: left;
  color: #00cabc;
  position: relative;
}

.login-checked {
  color: #cd3a0d;
  font-size: 0.3rem;
  position: absolute;
  left: 0.02rem;
}


